.myHexagon {
  fill: none;
  stroke-dasharray: 1200;
  stroke-dashoffset: 1200;
  animation: drawHex 1.5s ease-out forwards;
}

.myHexagon.outer {
  stroke-width: 4;
  animation-delay: 0s;
}

.myHexagon.inner {
  stroke-width: 3;
  animation-delay: 0.3s;
}

.rv-outline {
  font-family: "Playfair Display", serif;
  font-size: 50px;
  fill: none;
  stroke: white;
  stroke-width: 1.5;
  opacity: 0;
  animation: fadeIn 0.1s ease forwards;
  animation-delay: 0.1s;
}

/* Typewriter Name */
.animated-name {
  font-family: "Agustina", sans-serif;
  font-size: 70px;
  color: white;
  margin-top: -140px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: relative;
}

.type-char {
  opacity: 0;
  animation: typeIn 0.1s forwards;
}

@keyframes typeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes blink {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
}

@keyframes drawHex {
  to {
    stroke-dashoffset: 0;
  }
}

@keyframes fadeIn {
  to {
    opacity: 1;
  }
}

