@font-face {
  font-family: "Agustina";
  src: url("/masterPortfolio/assets/fonts/Agustina.woff") format("woff");
}

@font-face {
  font-family: "Montserrat";
  src: url("/masterPortfolio/assets/fonts/Montserrat-Regular.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Bold";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-Bold.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Bold Italic";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-BoldItalic.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Italic";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-Italic.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Medium";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-Medium.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Medium Italic";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-MediumItalic.ttf") format("truetype");
}

@font-face {
  font-family: "Google Sans Regular";
  src: url("/masterPortfolio/assets/fonts/GoogleSans-Regular.ttf") format("truetype");
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html,
body {
  scroll-behavior: smooth;
  overflow-x: hidden;
}

body {
  margin: 0;
  font: 19px / 23px "Montserrat", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (max-width: 1380px) {
  html,
  body {
    font-size: 16px;
    line-height: normal;
  }
}
