@font-face {
  font-family: "Agustina";
  src: url("/masterPortfolio/assets/fonts/Agustina.woff") format("woff");
}

.logo_wrapper {
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Agustina", "Montserrat", sans-serif;
}

.screen {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: 100%;
  animation: grow 1.5s forwards;
  font-family: "Agustina", "Montserrat", sans-serif;
}

body {
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

@keyframes grow {
  0% {
    transform: scale(0);
  }
  10%, 90% {
    transform: scale(1);
    border-radius: 0%;
    height: 100%;
    width: 100%;
  }
  100% {
    transform: scale(0);
    transform-origin: 50% 50%;
    border-radius: 100%;
  }
}

#logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
